import WalletConnectProvider from '@walletconnect/web3-provider';
import Fortmatic from "fortmatic";
import Portis from "@portis/web3";
import Authereum from "authereum";
import Torus from "@toruslabs/torus-embed";
import { ethers } from "ethers";
import BoltXLogo from "./boltx.svg";

export const providerOptions = {
  injected: {
    display: {
        logo: "data:image/svg+xml;base64,",
        name: "MetaMask",
        description: "Connect to your MetaMask Wallet"
    },
    package: null
  },
  "custom-boltx": {
    display: {
      logo: BoltXLogo,
      name: "BoltX",
      description: "Connect to your BoltX Wallet"
    },
    package: ethers.providers.Provider,
    connector: async () => {
      let provider = null;
      if (typeof (window as any).boltX !== 'undefined') {
        provider = (window as any).boltX.ethereum;
        try {
          await provider.request({ method: 'eth_requestAccounts' })
        } catch (error) {
          throw new Error("User Rejected");
        }
      } else {
        throw new Error("BoltX not found");
      }
      return provider;
    }
  },
  walletconnect: {
    package: WalletConnectProvider,
    options: {
        infuraId: process.env.REACT_APP_INFURA_ID
    }
  },
  fortmatic: {
    package: Fortmatic,
    options: {
        key: process.env.REACT_APP_FORTMATIC_KEY
    }
  },
  portis: {
    package: Portis,
    options: {
        id: process.env.REACT_APP_PORTIS_ID
    }
  },
  torus: {
    package: Torus
  },
  authereum: {
    package: Authereum
  },
};
